import { propsBinder, methodsBinder, addToParentElement } from '../utils/utils.js';
// import { DomEvent } from 'leaflet';
import * as L from 'leaflet';
const DomEvent = (L.default || L).DomEvent;

// from 'leaflet';

export default {
    props: {
        // 
        options: {
            type: Object,
            default: () => ({})
        },
        // ,truemapfalse
        addToMap: {
            type: Boolean,
            default: true
        },
        // leafletfalsetruethis.$refs['refName'].XXX
        useSelfMethods: {
            type: Boolean,
            default: true
        }
    },
    data() {
        return {
            // 
            ready: false,
            // 
            selfOptions: {},
            // leafletmapselfL.map,tileLayerselfL.tileLayer
            self: null,
        }
    },
    methods: {
        getOptions() {
            return this.selfOptions;
        },
        getSelf() {
            return this.self;
        },
        // 
        initFunction() {
            if (!this.self) return false;

            // leafletmap
            addToParentElement(this, this.lMap);

			let _attrs={}, _listeners={};
			let attrsObj = this.$attrs;
			Object.keys(attrsObj).forEach(k=>{
				if(k && k.startsWith('on')){
					let newK = k.substring(2)
					newK = newK && newK.toLowerCase()
					_listeners[newK] = attrsObj[k];
				}else{
					_attrs[k] = attrsObj[k];
				}
			});
            // leaflet
            DomEvent.on(this.self, _listeners);

            // 
            propsBinder(this, this.self, this.$options.props);
            propsBinder(this, this.self, {}, this.options);

            this.$nextTick(() => {
                this.ready = true;
                // 
                this.useSelfMethods && methodsBinder(this, this.self);
                // 
                this.$emit('ready', this.self, this.selfOptions);
            });
        }
    },
    beforeUnmount() {
        this.self && this.self.remove && this.self.remove();
    }
};
